import React from 'react';

export function Transaction({
    headers, 
    showCorrectHeader, 
    id, 
    date,
    description, 
    headerId, 
    amount, 
    isIncome,
    removeTransaction, 
    changeTransaction, 
    lang}) {

    function handleChangeHeader(e) {
        changeTransaction(id, description, e.target.value);
    }

    function handleChangeDescription(e) {
        changeTransaction(id, e.target.value, headerId);
    }

    function handleClick() {
        removeTransaction(id);
    }

    return(
            <tr className='Transaction'>
                <td className='column-date'>{date.substring(8,10) + '-' + date.substring(5, 7) + '-' + date.substring(0, 4)}</td>
                <td className='column-description'><input type='text' value={description} onChange={handleChangeDescription}/></td>
                <td className='column-header'><select value={Number(headerId)} onChange={handleChangeHeader}>
                                <option key='-1' value=''></option>
                                {headers.sort((a, b) => {
                                    if (showCorrectHeader(Number(a.id)) < showCorrectHeader(Number(b.id))) {
                                        return -1;
                                    }
                                    else {
                                        return 1;
                                    }
                                }).map((header, i) => {
                                    return <option key={i} value={header.id}>{showCorrectHeader(header.id)}</option>
                                })}
                            </select></td>
                 <td className={isIncome ? 'column-amount in' : 'column-amount out'}>&#8364; {Number(amount).toFixed(2)}</td>
                <td className='column-remove'><button onClick={handleClick}>&#215;</button></td>
           </tr>
     );
}