import React from 'react';

export function User({
    email,
    setEmail,
    password,
    setPassword,
    newEmail,
    setNewEmail,
    newPassword1,
    setNewPassword1,
    newPassword2,
    setNewPassword2,
    forgotPasswordEmail,
    setForgotPasswordEmail,
    login,
    register,
    getPassword,
    lang}) {

    function handleLogin(e) {
        e.preventDefault();
        login(email, password);
    }

    function handleRegister(e) {
        e.preventDefault();
        if (newEmail.length < 5) {
            alert(lang === 'en' ? 'E-mail needs to be at least 5 characters' : 'E-mail moet tenminste 5 tekens bevatten')
        }
        else if (newPassword1.length < 8 || newPassword2.length < 8) {
            alert(lang === 'en' ? 'Password needs to be at least 8 characters' : 'Wachtwoord moet tenminste 8 tekens bevatten')
        }
        else if (newPassword1 !== newPassword2) {
            alert(lang === 'en' ? 'Passwords do not match' : 'Wachtwoorden komen niet overeen')
        }
        else {
            register(newEmail, newPassword1)
        }
    }

    function handleForgotPassword(e) {
        e.preventDefault();
        getPassword(forgotPasswordEmail);
    }

   return (
        <div className='User'>
            <form>
                <h2>{lang === 'en' ? 'Login' : 'Inloggen'}</h2>
                <label htmlFor='email'>E-mail: </label>
                <input type='email' id='email' onChange={e => setEmail(e.target.value)}/>
                <label htmlFor='password'>{lang === 'en' ? 'Password: ' : 'Wachtwoord: '}</label>
                <input type='password' id='password' onChange={e => setPassword(e.target.value)}/>
                <button onClick={handleLogin}>{lang === 'en' ? 'Login' : 'Inloggen'}</button>
            </form>
            <br />
            <hr />
            <form>
                <h2>{lang === 'en' ? 'Register' : 'Registreer'}</h2>
                <label htmlFor='newemail'>E-mail: </label>
                <input type='email' id='newemail' onChange={e => setNewEmail(e.target.value)}/>
                <label htmlFor='newpassword1'>{lang === 'en' ? 'Password: ' : 'Wachtwoord: '}</label>
                <input type='password' id='newpassword1' onChange={e => setNewPassword1(e.target.value)}/>
                <label htmlFor='newpassword2'>{lang === 'en' ? 'Repeat password: ' : 'Herhaal wachtwoord: '}</label>
                <input type='password' id='newpassword2' onChange={e => setNewPassword2(e.target.value)}/>
                <button onClick={handleRegister}>{lang === 'en' ? 'Register' : 'Registreer'}</button>
            </form>
            <br />
            <hr />
            <form>
                <h2>{lang === 'en' ? 'Forgot password' : 'Wachtwoord vergeten'}</h2>
                <label htmlFor='forgotpasswordemail'>E-mail: </label>
                <input type='email' id='forgotpasswordemail' onChange={e => setForgotPasswordEmail(e.target.value)}/>
                <button onClick={handleForgotPassword}>{lang === 'en' ? 'Request password' : 'Wachtwoord opvragen'}</button>
            </form>
            <br />
            <hr />
        </div>
    );
}