import React from 'react';
import {HeaderTable} from '../HeaderTable/HeaderTable';
import {BarChart} from '../BarChart/BarChart'
import {PieChart} from '../PieChart/PieChart'

export function Overview({
    userId,
    transactions, 
    headers,
    showCorrectHeader, 
    fromDate, 
    setFromDate, 
    toDate, 
    setToDate, 
    addHeader, 
    newHeaderIn,
    setNewHeaderIn, 
    newHeaderOut, 
    setNewHeaderOut, 
    removeHeader, 
    showEmpty, 
    setShowEmpty, 
    view, 
    setView, 
    lang}) {

    function handleChange(e) {
        setShowEmpty(e.target.value === 'true' ? false : true);
    }

    function calculateTotalIncome() {
        return transactions
        .filter(transaction => transaction.date >= fromDate && transaction.date <= toDate)
        .filter(transaction => transaction.is_income)
        .reduce((a, b) => a + Number(b.amount), 0);
    }

    function calculateTotalExpenses() {
        return transactions
        .filter(transaction => transaction.date >= fromDate && transaction.date <= toDate)
        .filter(transaction => !transaction.is_income)
        .reduce((a, b) => a + Number(b.amount), 0);
    }

    //Calculates the total income and the income for each header separately, in the selected time period
    function calculateIncomePerHeader() {

        const incomeHeaders = {};
        for (let header of headers) {
            if (header.is_income) {
                incomeHeaders[header.id] = 0;
            }
        }

        //Filter transactions in selected time period
        let array = transactions.filter(transaction => transaction.date >= fromDate && transaction.date <= toDate);
        if (!array.length) return [incomeHeaders, 0];

        //For each transaction, check if header is in income headers. If so, update the amount for that header and the total amount
        for (let transaction of array) {
            if (transaction.header_id in incomeHeaders) {
                incomeHeaders[transaction.header_id] += Number(transaction.amount);
            }
         }
        return [incomeHeaders, calculateTotalIncome()];
    }

    //Calculates the total income and the income for each header separately, in the selected time period
    function calculateExpensesPerHeader() {

        const expenseHeaders = {};
        for (let header of headers) {
            if (!header.is_income) {
                expenseHeaders[header.id] = 0;
            }
        }

        //Filter transactions in selected time period
        let array = transactions.filter(transaction => transaction.date >= fromDate && transaction.date <= toDate);
        if (!array.length) return [expenseHeaders, 0];

        //For each transaction, check if header is in income headers. If so, update the amount for that header and the total amount
        for (let transaction of array) {
            if (transaction.header_id in expenseHeaders) {
                expenseHeaders[transaction.header_id] += Number(transaction.amount);
            }
         }
         return [expenseHeaders, calculateTotalExpenses()];
    }

    if (!userId) {
        return (
            <div className='Overview'>
                <p>{lang === 'en' ? 'Nobody is logged in' : 'Er is niemand ingelogd'}</p>
            </div>
        );
    }

    return (
        <div className='Overview'>
            <h2>{lang === 'en' ? 'View' : 'Weergave'}</h2>
            <label htmlFor='from'>{lang === 'en' ? 'From' : 'Van'}:</label>
            <input type='date' id='from' value={fromDate} onChange={e => setFromDate(e.target.value)}/>
            <label htmlFor='to'>{lang === 'en' ? 'To' : 'Tot'}:</label>
            <input type='date' id='to' value={toDate} onChange={e => setToDate(e.target.value)}/>
            <br />
            <br />
            <label>{lang === 'en' ? 'Chart:' : 'Diagram: '}</label>
            <input type='radio' name='view' id='table' value='table' checked={view === 'table'} onChange={e => setView(e.target.value)}/>
            <label htmlFor='table'>{lang === 'en' ? 'Table' : 'Tabel'}</label>
            <input type='radio' name='view' id='bars' value='bars' checked={view === 'bars'} onChange={e => setView(e.target.value)} />
            <label htmlFor='bar'>{lang === 'en' ? 'Bar' : 'Staaf'}</label>
            <input type='radio' name='view' id='pie' value='pie' checked={view === 'pie'} onChange={e => setView(e.target.value)} />
            <label htmlFor='pie'>{lang === 'en' ? 'Pie' : 'Taart'}</label>
            <br />
            <br />
            <label>{lang === 'en' ? 'Headers: ' : 'Rubrieken: '} </label>
            <input type='checkbox' id='empty' value={showEmpty} checked={showEmpty} onChange={handleChange}></input>
            <label htmlFor='empty'>{lang === 'en' ? 'Show empty headers' : 'Toon lege rubrieken'}</label>
            <br />
            <hr />
            <h2>{lang === 'en' ? 'Net result' : 'Netto resultaat'}</h2>
            <h3 className={calculateTotalIncome() - calculateTotalExpenses() < 0 ? 'neg' : 'pos'}>&#8364; {(calculateTotalIncome() - calculateTotalExpenses()).toFixed(2)}</h3>
            <br />
            <hr />
            <h2>{lang === 'en' ? 'Income' : 'Inkomsten'}</h2>
            {view === 'table' && <HeaderTable
            inout={'in'} 
            calculate={calculateIncomePerHeader} 
            showCorrectHeader={showCorrectHeader} 
            addHeader={addHeader} 
            newHeaderIn={newHeaderIn} 
            setNewHeaderIn={setNewHeaderIn} 
            removeHeader={removeHeader} 
            showEmpty={showEmpty} 
            lang={lang}/>}
            {view === 'bars' && <BarChart
            lang={lang}
            calculate={calculateIncomePerHeader} 
            showCorrectHeader={showCorrectHeader} 
            showEmpty={showEmpty}/>}
            {view === 'pie' && <PieChart
            lang={lang}
            calculate={calculateIncomePerHeader} 
            showCorrectHeader={showCorrectHeader} 
            showEmpty={showEmpty}/>}
            <br />
            <hr />
            <h2>{lang === 'en' ? 'Expenses' : 'Uitgaven'}</h2>
            {view === 'table' && <HeaderTable 
            inout={'out'} 
            calculate={calculateExpensesPerHeader} 
            showCorrectHeader={showCorrectHeader} 
            addHeader={addHeader}
            newHeaderOut={newHeaderOut} 
            setNewHeaderOut={setNewHeaderOut} 
            removeHeader={removeHeader} 
            showEmpty={showEmpty} 
            lang={lang} />}
            {view === 'bars' && <BarChart
            lang={lang}
            calculate={calculateExpensesPerHeader} 
            showCorrectHeader={showCorrectHeader} 
            showEmpty={showEmpty}/>}
            {view === 'pie' && <PieChart 
            lang={lang}
            calculate={calculateExpensesPerHeader} 
            showCorrectHeader={showCorrectHeader} 
            showEmpty={showEmpty}/>}
            <br />
            <hr />
            <br />
       </div>
    );
}
