import React from 'react';

export function HeaderTable({
    inout, 
    calculate, 
    showCorrectHeader, 
    addHeader,
    newHeaderIn, 
    setNewHeaderIn, 
    newHeaderOut, 
    setNewHeaderOut, 
    removeHeader, 
    showEmpty, 
    lang}) {

    let [headers, total] = calculate();

    function handleAddClick() {
        addHeader(inout === 'in' ? newHeaderIn : newHeaderOut, inout === 'in' ? true : false)
        inout === 'in' ? setNewHeaderIn('') : setNewHeaderOut('');
    }

    function handleRemoveClick(e) {
        removeHeader(Number(e.target.value));
    }

    return (
        <div className='Headertable'>
            <table>
                <thead>
                    <tr>
                        <th className='column-header'>{lang === 'en' ? 'Header' : 'Rubriek'}</th>
                        <th className='column-amount'>{lang === 'en' ? 'Amount' : 'Bedrag'}</th>
                        <th className='column-percentage'>%</th>
                        <th className='column-remove'></th>
                    </tr>
                </thead>
                <tbody>
                <tr>
                        <td className='column-header'><input type='text' value={inout === 'in' ? newHeaderIn : newHeaderOut} onChange={e => inout === 'in' ? setNewHeaderIn(e.target.value) : setNewHeaderOut(e.target.value)} /></td>
                        <td className='column-amount'></td>
                        <td className='column-percentage'></td>
                        <td className='column-remove'><button onClick={handleAddClick}>+</button></td>

                    </tr>
                    {Object.entries(headers).sort((a, b) => {
                        if (showCorrectHeader(Number(a[0])) < showCorrectHeader(Number(b[0]))) {
                            return -1;
                        }
                        else {
                            return 1;
                        }
                    }).map((el, i) => {
                            return (el[1] > 0 || showEmpty) && <tr key={i}>
                            <td className='column-header'>{showCorrectHeader(Number(el[0]))}</td>
                            <td className={'column-amount ' + inout}>&#8364; {Number(el[1]).toFixed(2)}</td>
                            <td className='column-percentage'>{total > 0 ? (Number(el[1]) / total * 100).toFixed(0) : 0}</td>
                            <td className='column-remove'><button value={Number(el[0])} onClick={handleRemoveClick}>&#215;</button></td>
                        </tr>
                        })}
                    <tr>
                        <td className='column-header'><b>{lang === 'en' ? 'Total' : 'Totaal'}</b></td>
                        <td className={'column-amount ' + inout}><b>&#8364; {total.toFixed(2)}</b></td>
                        <td className='column-percentage'>{total > 0 ? 100 : 0}</td>
                        <th className='column-remove'></th>
                    </tr>
                </tbody>
            </table>
        </div>
    );
    
}