import React from 'react';

export function Import({lang, inputField, importTransactions}) {
    return (
        <div className='Import'>
            <h2>{lang === 'en' ? 'Import' : 'Importeer'}</h2>
            <input type='file' id='import' ref={inputField} />
            <button id='import' onClick={importTransactions}>{lang === 'en' ? 'Import' : 'Importeer'}</button>
        </div>
    );
}