import React from 'react';

export function Filter({
    lang,
    headers, 
    showCorrectHeader, 
    filterFromDate, 
    setFilterFromDate,
    filterToDate,
    setFilterToDate, 
    filterDescription, 
    setFilterDescription,
    setFilterHeaders, 
    filterFromAmount, 
    setFilterFromAmount, 
    filterToAmount, 
    setFilterToAmount, 
    resetFilter}) {

    return (
        <div className='Filter'>
        <h2>Filter</h2>
        <table>
            <tbody>
                <tr>
                    <td><label htmlFor='fromdate'>{lang === 'en' ? 'From:  ' : 'Van: '}</label></td>
                    <td><input type='date' value={filterFromDate} id='fromdate' onChange={e => setFilterFromDate(e.target.value)}></input></td>
                    <td><label htmlFor='todate'>{lang === 'en' ? 'To: ' : 'Tot: '}</label></td>
                    <td><input type='date' value={filterToDate} id='todate' onChange={e => setFilterToDate(e.target.value)}></input></td>
                </tr>
                <tr>
                    <td><label htmlFor='description'>{lang === 'en' ? 'Description: ' : 'Omschrijving: '}</label></td>
                    <td><input type='text' id='description' value={filterDescription} onChange={e => setFilterDescription(e.target.value)}/></td>
                    <td><label htmlFor='headers'>{lang === 'en' ? 'Headers: ' : 'Rubrieken: '}</label></td>
                    <td>
                        <select id='headers' name='headers' multiple onChange={e => setFilterHeaders(Array.from(e.target.options).filter(option => option.selected).map(option => option.value))}>
                        {headers.sort((a, b) => {
                            if (showCorrectHeader(Number(a.id)) < showCorrectHeader(Number(b.id))) {
                                return -1;
                            }
                            else {
                                return 1;
                            }
                        }).map((header, i) => {
                            return <option key={i} value={header.id}>{showCorrectHeader(header.id)}</option>
                        })}
                        </select>
                    </td>
                </tr>
                <tr>
                    <td><label htmlFor='fromamount'>{lang === 'en' ? 'From amount: ' : 'Vanaf bedrag: '}</label></td>
                    <td><input type='number' id='fromamount' value={filterFromAmount} onChange={e => setFilterFromAmount(e.target.value)}></input></td>
                    <td><label htmlFor='toamount'>{lang === 'en' ? 'To amount: ' : 'Tot bedrag: '}</label></td>
                    <td><input type='number' id='toamount' value={filterToAmount} onChange={e => setFilterToAmount(e.target.value)}></input></td>
                </tr>
            </tbody>
        </table>
        <br />
        <button onClick={resetFilter}>Reset filter</button>
    </div>
    );
}