import React from 'react';
import {Transaction} from '../Transaction/Transaction';

export function List({
    transactions, 
    headers, 
    showCorrectHeader, 
    date, 
    setDate, 
    description, 
    setDescription,
    headerId, 
    setHeaderId,
    amount,
    setAmount, 
    addTransaction, 
    removeTransaction,
    changeTransaction, 
    lang, 
    maxItemsOnPage, 
    setMaxItemsOnPage}) {

    let itemsOnPage = 0;


    function handleClick() {
        addTransaction(date, description, headerId, amount);
    }

    function showMoreItems() {
        setMaxItemsOnPage(prev => prev + 50);
    }
    
    return (
        <div className='List'>
           <h2>{lang === 'en' ? 'List' : 'Lijst'}</h2>
            <p>{lang === 'en' ? `Showing ${Math.min(maxItemsOnPage, transactions.length)} of ${transactions.length} transactions` : `${Math.min(maxItemsOnPage, transactions.length)} van ${transactions.length} transacties tonend`}</p>
            <table>
                <thead>
                    <tr>
                        <th className='column-date'>{lang === 'en' ? 'Date' : 'Datum'}</th>
                        <th className='column-description'>{lang === 'en' ? 'Description' : 'Omschrijving' }</th>
                        <th className='column-header'>{lang === 'en' ? 'Header' : 'Rubriek'}</th>
                        <th className='column-amount'>{lang === 'en' ? 'Amount' : 'Bedrag'}</th>
                        <th className='column-remove'></th>
                    </tr>
               </thead>
                <tbody>
                    <tr>
                        <td className='column-date'><input type='date' onChange={e => setDate(e.target.value)} /></td>
                        <td className='column-description'><input type='text' value={description} onChange={e => setDescription(e.target.value)}/></td>
                        <td className='column-header'><select value={headerId} onChange={e => setHeaderId(Number(e.target.value))}>
                                <option key='-1' value=''></option>
                                {headers.map((header, i) => {
                                    return <option key={i} value={header.id}>{showCorrectHeader(header.id)}</option>
                                })}
                            </select></td>
                        <td className='column-amount'><input type='number' value={Number(amount)} onChange={e => setAmount(Number(e.target.value))}/></td>
                        <td className='column-remove'><button onClick={handleClick}>+</button></td>
                     </tr>
                    {transactions.map((transaction, i) => {
                        itemsOnPage++;
                        return itemsOnPage <= maxItemsOnPage && <Transaction
                        key={i}
                        headers={headers}
                        showCorrectHeader={showCorrectHeader}
                        id={transaction.id}
                        date={transaction.date} 
                        description={transaction.description}
                        headerId={transaction.header_id} 
                        amount={transaction.amount} 
                        isIncome={transaction.is_income}
                        removeTransaction={removeTransaction} 
                        changeTransaction={changeTransaction} 
                        lang={lang}/>
                    })}
                </tbody>
            </table>
                <button className={maxItemsOnPage < transactions.length ? 'show-more-button show' : 'show-more-button hide'} onClick={showMoreItems}>{lang === 'en' ? 'Show more' : 'Toon meer'}</button>
        </div>
    );
}