const Server = {

    host: 'https://xpensoft.herokuapp.com',

    async login(email, password) {
            const response = await fetch(`${this.host}/login`, {
                method: 'GET',
                headers: {'Content-Type': 'application/json', email, password},
            });
            if (response.status === 401) {
                throw new Error();
            }
            const jsonResponse = await response.json();
            return jsonResponse;
    },

    async register(newEmail, newPassword) {
             const response = await fetch(`${this.host}/register`, {
                method: 'POST',
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify({newEmail, newPassword})
            });
            if (response.status === 401) {
                throw new Error();
            }
            const jsonResponse = await response.json();
            return jsonResponse;
     },

     async getPassword(email) {
        const response = await fetch(`${this.host}/getpassword/${email}`);
        if (response.status === 404) {
            throw new Error();
        }
        const jsonResponse = await response.json();
        return jsonResponse;
    },

    async getHeaders(userId, email, password) {
            const response = await fetch(`${this.host}/headers/${userId}`, {
                method: 'GET',
                headers: {'Content-Type': 'application/json', userId, email, password}
            });
            const jsonResponse = await response.json();
            return jsonResponse;
     },
    
    async addHeader(name, isIncome, userId, email, password) {
            const response = await fetch(`${this.host}/headers`, {
                method: 'POST',
                headers: {'Content-Type': 'application/json', userId, email, password},
                body: JSON.stringify({name, isIncome, userId})
            });
            const jsonResponse = await response.json();
            return jsonResponse;
    },

    async deleteHeader(id, userId, email, password) {
            const response = await fetch(`${this.host}/headers/${id}`, {
                method: 'DELETE',
                headers: {'Content-Type': 'application/json', userId, email, password}
            })
            return response;
     },

    async getTransactions(userId, email, password) {
             const response = await fetch(`${this.host}/transactions/${userId}`, {
                method: 'GET',
                headers: {'Content-Type': 'application/json', userId, email, password},
            });
            const jsonResponse = await response.json();
            return jsonResponse;
    },

    async addTransaction(date, description, headerId, amount, userId, email, password) {
             const response = await fetch(`${this.host}/transactions`, {
                method: 'POST',
                headers: {'Content-Type': 'application/json', userId, email, password},
                body: JSON.stringify({date, description, headerId, amount, userId})
            });
            const jsonResponse = await response.json();
            return jsonResponse;
   },

    async changeTransaction(id, description, headerId, userId, email, password) {
             const response = await fetch(`${this.host}/transactions/${id}`, {
                method: 'PUT',
                headers: {'Content-Type': 'application/json', userId, email, password},
                body: JSON.stringify({description, headerId})
            });
            const jsonResponse = await response.json();
            return jsonResponse;
       },

     async deleteTransaction(id, userId, email, password) {
            const response = await fetch(`${this.host}/transactions/${id}`, {
                method: 'DELETE',
                headers: {'Content-Type': 'application/json', userId, email, password},
            })
            return response;
    }, 
 }

export default Server;